import React from 'react';
import { HomepageCallout } from 'gatsby-theme-carbon';
import HomepageTemplate from 'gatsby-theme-carbon/src/templates/Homepage';
import HomepageVideo from '../../components/HomepageVideo';
import { gray10 } from '@carbon/colors';

// Component to be shadowed

const FirstLeftText = () => (
  <p>
    IBM Fusion
  </p>
);

const FirstRightText = () => (
  <p>
    Fusion is available as two flexible options to support your hybrid cloud needs.
    <span style={{ paddingTop: "1.25rem", display : "flex" }}>↓</span>
  </p>
);

const customProps = {
Banner: (
    <HomepageVideo
        src="videos/hero-video-min.mp4"
        poster="images/fusion-overview-hero.jpg"
    />
    ),
  FirstCallout: (
    <HomepageCallout
      backgroundColor={gray10}
      color={"#191919"}
      leftText={FirstLeftText}
      rightText={FirstRightText}
    />
  ),
  SecondCallout: null,
};

// spreading the original props gives us props.children (mdx content)
function ShadowedHomepage(props) {
  return <HomepageTemplate {...props} {...customProps} />;
}

export default ShadowedHomepage;
